/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
body{
  img{
    width: 100%;
    height: auto;
  }
}

.site {
  max-width: auto;
  margin: 0 auto;
}

.site-content {
  padding-top: 50px;
  margin: 5rem 0;
  display: flex;
}

.main-navigation{
  margin-top:-50px;
}

body:not(.home):not(.page-id-7):not(.post-type-archive):not(.tax-product-type):not(.single-product):not(.single-adventure) .site-content {
 
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto;
  margin-top: auto;
  width: 1120px;
}

.content-area {
width:73%;
  padding-top: 3rem;
}

.widget-area {
  width:27%;
  padding-top: 3rem;
}

/* Front page */

h2 {
  font-family: 'Novecento Sans Web';
  color: #2f1339;
  font-size: 2.1rem;
  margin-bottom: 1.5rem;
}

p {
  font-family: 'Merriweather Web';
  color: #404040;
  font-size: 16px;
  margin-bottom: 1.5rem;
}

// Singles

.single{

  
  .comment-body {
		display: flex;
		padding-top: 1.25rem;
		padding-bottom: 1.5rem;
		p {
			margin: .5rem 0 1rem 0;
		}
	}

	.comment-author-avatar {
		padding-right: 3rem;
		img {
			border-radius: 50%;
			width: 64px;
			height: 64px;
    }
    a{
      color: #d99054;
    }
	}

  cite {
		font-weight: bold;
    font-style: normal;
    font-family: 'Merriweather Web';
	}

 .comments-link{
   a{
    color: #fff;
   }
   
 }
  .comment-metadata {
		color: grey;
    margin-bottom: 1rem;
    font-family: 'Merriweather Web';
    a{
      color: $color__greenbrand;
      &:hover {
        color: #111;
      }
    }
  
	}
  #reply-title, .comment-reply-title h3{
    font-size: 2rem;
  }
  .cat-links, .tags-links{
    margin-right: 30px;
    text-transform: uppercase;
  }
  .content-area{
    padding-bottom: 3rem;
  }
  .comment-form-cookies-consent{
    display: none;
  }
  .nav-previous, .nav-next{
    display: none;
  }

  .comment-form-comment, .comment-form-author, .comment-form-email, .comment-form-url {
    label{
      text-transform: uppercase;
      font-family: 'Novecento Sans Web';
    }
    .required {
      color: #d99054;
  }
  }
  .comment-reply-title{
    h3{
      font-size: 3rem;
    }
  }
  .comment-form {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		line-height: 1;
		input {
			margin: 1rem 0 0 0;
		}
	
		.comment-form-comment {
			width: 100%;
			display: flex;
			flex-direction: column;
			textarea {
				margin-bottom: 0;
				margin-top: 1rem;
			}
		}
		.comment-form-author {
			width: 49%;
			display: flex;
			flex-direction: column;
			margin-right: 2%;
		}
		.comment-form-email {
			width: 49%;
			display: flex;
			flex-direction: column;
		}
		.comment-form-url {
			width: 100%;
			display: flex;
			flex-direction: column;
		}
		.form-submit {
			margin-bottom: 3.5rem;
		}
	}
}
.social-buttons{
  margin-top: 40px;
  margin-bottom: 40px;
    a {
      color: $black;
      text-decoration: none;
      padding: 0.75rem 1.5rem;
      border: 1px solid #000;
      font-size: 0.9rem;
      line-height: 1;
      text-transform: uppercase;
      

      &:hover {
        background-color: $color__greenbrand;
        border: 1px solid $color__greenbrand;
        color: $color__background-body;
      }
    }

    i {
      margin-right: 10px;
    }
  .comment-metadata{
    a{
      color: $color__greenbrand
    }
  }
  .comment-content{
    p{
      margin-top:5rem;
    }
  }
  .comment-respond{
  .coment-reply-title{
      font-size: 2rem;
    }
  }
  #respond{
    h3{
      font-size: 4rem;
    }
    
  }
  
  }

  // Find Us

.page-id-23{

  .main-navigation{

    a{
      &:hover{
        color: #2f1339;
      }
     
    }
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      color: $color__link;
      &:hover {
        color: #2f1339;
      }
    }
  }
    .entry-content{
      .wp-caption, embed, iframe, object {
        
        width: 760px;
        height: 300px;
    }
    }

    .content-area{
      padding-bottom: 5rem;
    }
    
    input[type="text"], input[type="url"], input[type="email"] {
    padding: 3px;
    width: 50%;
    }
    .site-header {
    background-color: rgba(255, 255, 255, 0.75);
    border-bottom: 1px solid #e1e1e1;
    padding: 0.5rem 0;
    position: fixed;
    transition: top 0.5s linear;
    width: 100%;
    display: inline-block;
    height: 50px;
    

    h1 {
      background: url(http://localhost/wptest/wp-content/themes/inhabitent_projekt/resources/images/logos/inhabitent-logo-tent.svg)
        no-repeat;
      background-size: 50px 33px;
      margin: 0;
    }

    a {
      color: $color__greenbrand;
    }
  }

  .content-area {
    border-right: 1px solid #e1e1e1;
    padding-right: 5%;
    width: 73%;
  }
  .wpcf7-form{
     .required{
      color: #d99054;
     }
      p{
        label{font-family: 'Novecento Sans Web';
        }
      }
      
  }
}    

// Front-page

.home{
  .main-navigation{
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      color: #fff;
      &:hover {
        color: #f0e4d1;
      }
    }
  }
  .main-navigation .search-field.search-visible {
    color: #ffff;
}
}
  
